.btn {
	width: 100%;
	border-radius: 1rem;
	box-shadow: 5px 5px 15px 0.5px rgba(37, 37, 37, 0.45);
	@extend %fontFamily;
	font-weight: 400;
	line-height: 1;
	transition: all 0.3s ease;

	&:focus {
		box-shadow: none;
	}
	&.disabled,
	&:disabled {
		opacity: 1;
	}
}
.btn-primary {
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-primary:hover {
	box-shadow: none;
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-primary:focus,
.btn-primary.focus {
	box-shadow: none;
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-primary.dropdown-toggle:focus {
	box-shadow: none;
}
.btn-secondary {
	border-color: $white;
	background-color: $white;
	color: $mainTxtDark;
}
.btn-secondary span {
	position: relative;
}
.btn-secondary:hover {
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-secondary:focus,
.btn-secondary.focus {
	box-shadow: none;
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.show > .btn-secondary.dropdown-toggle {
	border-color: $mainRed;
	background-color: $mainRed;
	color: $white;
}
.btn-secondary:not(:disabled):not(.disabled):active:focus,
.btn-secondary:not(:disabled):not(.disabled).active:focus,
.show > .btn-secondary.dropdown-toggle:focus {
	box-shadow: none;
}
